import * as React from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import logo from '../assets/background/logo.jpg';
import Typography from '@mui/material/Typography';
import { Avatar, Box, CardMedia, CircularProgress, Grid } from '@mui/material';
import { Link } from 'react-router-dom';
import Axios, { baseURL } from '../Axios';
import { useState } from 'react';
import Appbar from '../components/Appbar';
import ListContainer from '../components/List';


const Subcription = ()=>{
  const [loading  , setLoading ]= useState(false)
  const [subcription , setSubcrptions] = useState([])
  const fetchSubcription = async()=>{
    try {
      setLoading(true)      
      const {data} = await Axios.get("/getsubcription")
      setLoading(false)
      setSubcrptions(data.subcriptions)
    } catch (error) {
      setLoading(false)
      console.log(error)
    }
  }
  React.useEffect(()=>{
    fetchSubcription()
  },[])
    return(
        <>
        <Appbar/>
        <Grid container>
      <Grid item xs={2} sx={{display:{xs:"none",md:"block"}}}>
        <Box sx={{width:"16.5vw",borderRight:"1px solid lightGray",height:"90vh",position:"fixed"}}>
        <ListContainer/>
        </Box>
      </Grid>
      <Grid item xs={12} md={10} marginTop={"70px"}>
      {loading ? <Box sx={{ width: "100%", height: "85vh", display: "flex", justifyContent: "center", alignItems: "center" }}>
                <Box sx={{ position: 'relative' }} >
                  <img
                    style={{ width: "50px", height: "50px", objectFit: "cover", position: "absolute", left: "5px", top: "0px" }}
                    src={logo}
                    alt='logo'
                    aria-label="save"
                    color="primary"
                  />
                  <CircularProgress size={60} sx={{ marginRight: "-10px", marginTop: "-5px", color: "#ccc" }} />
                  <Typography sx={{ color: "#ccc", fontSize: "20px", marginLeft: "-12px" }}>Loading...</Typography>
                </Box>
              </Box> :<Grid container  gap={3} sx={{display:'flex',justifyContent:"center",alignItems:"center"}}>
        {
          subcription.map((elm,ind)=>{
            return(
              <Grid key={ind}>
                <Card sx={{ maxWidth: 345,maxHeight:320,bgcolor:"black",boxShadow:`2px 1px 5px 1px #ccc`}}>
        <CardMedia
        sx={{ height: 200 }}
        image={`${baseURL}/public/images/${elm.image.slice(14)}`}
        title={elm.name}
      />
              <CardContent>
              <Link to={`/dashboad/user/${elm._id}`} style={{display:"flex",color:"#ccc",columnGap:"10px",alignItems:"center",height:"50px"}}>
              {
                    "video.OwenerProfile"?<Box sx={{width:"50px",height:"50px",position:"relative"}}>
                    <img src={`${baseURL}/public/images/${elm.image.slice(14)}`}
                    style={{width:"100%",height:"100%",objectFit:"cover",position:"absolute",borderRadius:"50%"}}
                  alt={"video.OwenerProfile"} />
                  </Box>:<Avatar/>
                  }
                <Box>
                <Typography variant="h5" component="div" sx={{width:"250px"}}>
                 {elm.name}
                </Typography>
                <Typography variant="h5" component="div" sx={{width:"250px"}}>
                 {elm.email}
                </Typography>
                </Box>
                </Link>  
              </CardContent>
              <CardActions sx={{display:"flex",justifyContent:"space-between",alignItems:"center",marginTop:-1}}>
                <Typography variant="body2" color="text.secondary" sx={{color:"#ccc"}}>{elm.subcriber.length} subcribers</Typography>
              </CardActions>
            </Card>
              </Grid>
            )
          })
        }
        </Grid>}
      </Grid>
    </Grid>
        </>
    )
}

export default Subcription