import * as React from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import WatchLaterRoundedIcon from '@mui/icons-material/WatchLaterRounded';
import ThumbUpRoundedIcon from '@mui/icons-material/ThumbUpRounded';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import Typography from '@mui/material/Typography';
import { styled, alpha } from '@mui/material/styles';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import { Alert, Avatar, Box, Snackbar } from '@mui/material';
import { ThumbUpAltOutlined } from "@mui/icons-material";
import { Link } from 'react-router-dom';
import Axios, { baseURL } from '../Axios';
import { useState } from 'react';


const StyledMenu = styled((props) => (
    <Menu
      elevation={0}
      anchorOrigin={{
        vertical: 'bottom',
        horizontal: 'right',
      }}
      transformOrigin={{
        vertical: 'top',
        horizontal: 'right',
      }}
      {...props}
    />
  ))(({ theme }) => ({
    '& .MuiPaper-root': {
      borderRadius: 6,
      marginTop: theme.spacing(1),
      minWidth: 180,
      color:
        theme.palette.mode === 'light' ? 'rgb(55, 65, 81)' : theme.palette.grey[300],
      boxShadow:
        'rgb(255, 255, 255) 0px 0px 0px 0px, rgba(0, 0, 0, 0.05) 0px 0px 0px 1px, rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px',
      '& .MuiMenu-list': {
        padding: '4px 0',
      },
      '& .MuiMenuItem-root': {
        '& .MuiSvgIcon-root': {
          fontSize: 18,
          color: theme.palette.text.secondary,
          marginRight: theme.spacing(1.5),
        },
        '&:active': {
          backgroundColor: alpha(
            theme.palette.primary.main,
            theme.palette.action.selectedOpacity,
          ),
        },
      },
    },
  }));

const Video = ({video})=>{
    const [anchorEl, setAnchorEl] = useState(null);
    const [like , setLike] = useState(null)
    const [message , setMessage] = useState({})
    const [opens , setOpens] = useState(false)
    const open = Boolean(anchorEl);
    const handleClick = (event) => {
      setAnchorEl(event.currentTarget);
    };
    const handleClose = () => {
      setAnchorEl(null);
    };
    const watchLaterHandler = async(id)=>{
      try {
        const {data} =await Axios.post("/watchlater",{id})
        setMessage(data)
        setOpens(true)
        setAnchorEl(null);
      } catch (error) {
        setMessage(error.response.data)
        setOpens(true)
      }
    }

    const likeVideoHandler = async(id)=>{
      try {
        const {data} =await Axios.post("/likevideo",{id})
        setLike(data.like)
        setMessage(data)
        setOpens(true)
        setAnchorEl(null);
      } catch (error) {
        setMessage(error.response.data)
        setOpens(true)
      }
    }
    return(
            <>
              <Card sx={{ maxWidth: 345,maxHeight:320,bgcolor:"black",boxShadow:`2px 1px 5px 1px #ccc`}}>
              <Box className={'ListimageContainer'} sx={{width:{xs:"345px"},height:{xs:"22vh",sm:"30vh"}}}><video
              preload='auto'
              muted
              src={`${baseURL}/public/videos/${video.video.slice(14)}`}/></Box>
              <CardContent>
              <Link to={`/dashboad/videos/${video._id}`} style={{display:"flex",color:"#ccc",columnGap:"10px",alignItems:"center",height:"50px"}}>
              {
                    video.OwenerProfile?<Box sx={{width:"50px",height:"50px",position:"relative"}}>
                    <img src={`${baseURL}/public/images/${video.OwenerProfile.slice(14)}`}
                    style={{width:"100%",height:"100%",objectFit:"cover",position:"absolute",borderRadius:"50%"}}
                  alt={video.OwenerProfile} />
                  </Box>:<Avatar/>
                  }
                <Typography variant="h5" component="div" sx={{width:"250px"}}>
                 {video.title.length>48?`${video.title.slice(0,48)}...`:video.title} 
                </Typography>
                </Link>  
              </CardContent>
              <CardActions sx={{display:"flex",justifyContent:"space-between",alignItems:"center",marginTop:-1}}>
                <Typography variant="body2" color="text.secondary" sx={{color:"#ccc"}}>{video.veiws.length} veiw's</Typography>
              <MoreVertIcon sx={{color:"#ccc"}} onClick={handleClick}/>
              <StyledMenu
        id="demo-customized-menu"
        MenuListProps={{
          'aria-labelledby': 'demo-customized-button',
        }}
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
      >
        <MenuItem onClick={()=>watchLaterHandler(video._id)} disableRipple>
          <WatchLaterRoundedIcon/>
          <Typography>Watch Later</Typography>
        </MenuItem>
        <MenuItem onClick={()=>likeVideoHandler(video._id)} disableRipple>
          {like?<ThumbUpAltOutlined/>:<ThumbUpRoundedIcon/>}
          <Typography>{like?"unLike":"Like"}</Typography>
        </MenuItem>
      </StyledMenu>
              </CardActions>
            </Card>
            <Snackbar autoHideDuration={3000} open={opens} onClose={()=>setOpens(false)}>
            <Alert onClose={()=>setOpens(false)} variant='filled' severity={message.success?"success":"error"}>
                {message.message}
            </Alert>
        </Snackbar>
      </>        
    )
}

export default Video