import React, { useRef, useState } from "react";
import {Alert, Box, Button, Grid, Input, Snackbar, Typography} from '@mui/material';
import VideoLibraryRoundedIcon from '@mui/icons-material/VideoLibraryRounded';
import Appbar from "../components/Appbar";
import ListContainer from "../components/List";
import Axios from "../Axios";



const AddVideo = ()=>{
    const reset = useRef()
    const [title , setTitle] = useState("")
    const [message , setMessage] = useState({})
    const [open , setOpen] = useState(false)
    const [addvideo , setAddvideo] = useState("")

    const submitHandler = async(e)=>{
        e.preventDefault()
        try {
            
            const video = new FormData()
            video.append("title",title)
            video.append("video",addvideo)
            const {data}= await Axios.post("/addvideo",video)
            setTitle("")
            setAddvideo(null)
            reset.current.value =""
            setMessage(data)
            setOpen(true)
        } catch (error) {
            setMessage(error.reponse.data)
            setOpen(true)
        }
    }
    return(
        <>
        <Appbar/>
        <Grid container>
      <Grid item xs={2} sx={{display:{xs:"none",md:"block"}}}>
        <Box sx={{width:"16.5vw",borderRight:"1px solid lightGray",height:"90vh",position:"fixed"}}>
        <ListContainer/>
        </Box>
      </Grid>
      <Grid item xs={12} md={10}>
        <Grid container  marginTop={"60px"}>
            <form component={"form"} onSubmit={submitHandler} className="addvideo">
                <Box sx={{width:"250px", height:"300px"}}>
                    <Box sx={{width:"100%"}}><VideoLibraryRoundedIcon sx={{color:"#ccc"}}/></Box>
                    <Typography sx={{color:"#ccc",width:"100%"}}>Add Video___</Typography>
                <Input
                sx={{color:"#ccc",width:"100%"}}
                onChange={(e)=>setTitle(e.target.value)}
                value={title}
                placeholder="Enter Title's"/>
                <label htmlFor="inputTag" className="label">
                        <input type='file'
                            className='signUpProfile'
                            name='video'
                            accept="video/*"
                            onChange={(e)=>setAddvideo(e.target.files[0])}
                            ref={reset}
                        />
                    </label>
                <Button variant={"contained"} sx={{width:"100%"}} type={"submit"}>post</Button>
                </Box>
            </form>
        </Grid>
      </Grid>
    </Grid>
    <Snackbar onClose={()=>setOpen(false)} open={open}  autoHideDuration={3000}>
        <Alert onClose={()=>setOpen(false)} variant={"filled"} severity={message.success?"success":"error"}>
            {message.message}
        </Alert>
    </Snackbar>
        </>
    )
}

export default AddVideo