import React, { useEffect, useState } from 'react';
import ListContainer from '../components/List'
import { Box, CircularProgress, Grid, Typography } from '@mui/material'
import Appbar from '../components/Appbar';
import Components from '../components/Components';
import Axios from '../Axios';
import { ThumbDownRounded } from "@mui/icons-material";
import logo from '../assets/background/logo.jpg'



const Like = () => {
  const [loading, setLoading] = useState(false)
  const [likevideo, setLikevideo] = useState([])
  const fetchLikevideo = async () => {
    try {
      setLoading(true)
      const { data } = await Axios.get("/showlikevideo")
      setLoading(false)
      setLikevideo(data.showLikeVideo)
    } catch (error) {
      setLoading(false)
      console.log(error)
    }
  }
  useEffect(() => {
    fetchLikevideo()
  }, [])
  return (
    <>
      <Appbar />
      <Grid container>
        <Grid item xs={2} sx={{ display: { xs: "none", md: "block" } }}>
          <Box sx={{ width: "16.5vw", borderRight: "1px solid lightGray", height: "90vh", position: "fixed" }}>
            <ListContainer />
          </Box>
        </Grid>
        <Grid item xs={12} md={10}>
          <Grid container  >
            {
              loading ? <Box sx={{ width: "100%", height: "85vh", display: "flex", justifyContent: "center", alignItems: "center" }}>
                <Box sx={{ position: 'relative' }} >
                  <img
                    style={{ width: "50px", height: "50px", objectFit: "cover", position: "absolute", left: "5px", top: "0px" }}
                    src={logo}
                    alt='logo'
                    aria-label="save"
                    color="primary"
                  />
                  <CircularProgress size={60} sx={{ marginRight: "-10px", marginTop: "-5px", color: "#ccc" }} />
                  <Typography sx={{ color: "#ccc", fontSize: "20px", marginLeft: "-12px" }}>Loading...</Typography>
                </Box>
              </Box> : <Components title={"Like Videos"} video={likevideo} icon={<ThumbDownRounded />}
                fetchdata={fetchLikevideo} option={"Unlike"} />
            }
          </Grid>
        </Grid>
      </Grid>
    </>
  )
}

export default Like