import React, { useEffect, useState } from 'react';
import Axios, { baseURL } from '../Axios';
import ListContainer from '../components/List'
import { Box, Grid, MenuItem, Typography, Divider, Button, ListItem, Menu, List, CircularProgress } from '@mui/material'
import Appbar from '../components/Appbar'
import ShuffleIcon from '@mui/icons-material/Shuffle';
import PlayCircleIcon from '@mui/icons-material/PlayCircle';
import me from '../assets/img/me.jpg';
import { Link } from 'react-router-dom';
import { styled, alpha } from '@mui/material/styles';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import WatchLaterRoundedIcon from '@mui/icons-material/WatchLaterRounded';
import logo from '../assets/background/logo.jpg'


const StyledMenu = styled((props) => (
  <Menu
    elevation={0}
    anchorOrigin={{
      vertical: 'bottom',
      horizontal: 'right',
    }}
    transformOrigin={{
      vertical: 'top',
      horizontal: 'right',
    }}
    {...props}
  />
))(({ theme }) => ({
  '& .MuiPaper-root': {
    borderRadius: 6,
    marginTop: theme.spacing(1),
    minWidth: 180,
    color:
      theme.palette.mode === 'light' ? 'rgb(55, 65, 81)' : theme.palette.grey[300],
    boxShadow:
      'rgb(255, 255, 255) 0px 0px 0px 0px, rgba(0, 0, 0, 0.05) 0px 0px 0px 1px, rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px',
    '& .MuiMenu-list': {
      padding: '4px 0',
    },
    '& .MuiMenuItem-root': {
      '& .MuiSvgIcon-root': {
        fontSize: 18,
        color: theme.palette.text.secondary,
        marginRight: theme.spacing(1.5),
      },
      '&:active': {
        backgroundColor: alpha(
          theme.palette.primary.main,
          theme.palette.action.selectedOpacity,
        ),
      },
    },
  },
}));


const VideoHistory = () => {
  const [loading, setLoading] = useState(false)
  const [history, setHistory] = useState([])
  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  const fetchHIstory = async () => {
    try {
      setLoading(true)
      const { data: { history } } = await Axios.get("/showhistory")
      setLoading(false)
      setHistory(history)
    } catch (error) {
      setLoading(false)
      console.log(error)
    }
  }

  useEffect(() => {
    fetchHIstory()
  }, [])
  return (
    <>
      <Appbar />
      <Grid container>
        <Grid item xs={2} sx={{ display: { xs: "none", md: "block" } }}>
          <Box sx={{ width: "16.5vw", borderRight: "1px solid lightGray", height: "90vh", position: "fixed" }}>
            <ListContainer />
          </Box>
        </Grid>
        <Grid item xs={12} md={10}>
          
          {
            loading?<Box sx={{width:"100%",height:"85vh",display:"flex",justifyContent:"center",alignItems:"center"}}>
            <Box sx={{position:'relative'}} >
            <img
            style={{width:"50px",height:"50px",objectFit:"cover",position:"absolute",left:"5px",top:"0px"}}
            src={logo}
            alt='logo'
              aria-label="save"
              color="primary"
            />
            {loading && <CircularProgress size={60} sx={{marginRight:"-10px",marginTop:"-5px",color:"#ccc"}} />}
            <Typography sx={{color:"#ccc",fontSize:"20px",marginLeft:"-12px"}}>Loading...</Typography>
          </Box>
            </Box>:<Grid container  >
            <Grid item xs={4} sx={{ display: { xs: "none", md: "block" } }}>
              <Box className={"videoBanner"} sx={{ width: "100%", height: "100vh" }} >
                <Box sx={{ display: "flex", justifyContent: "center", flexDirection: "column" }}>
                  <Box className={"imageContainer"}><img src={me} alt="this is me" /></Box>
                  <Typography variant={"h4"} sx={{ fontWeight: "600", marginTop: "20px", color: "#ccc" }}></Typography>
                  <Typography variant={"secondary-body2"} sx={{ marginTop: { xs: "10px", sm: "20px", color: "#ccc" } }} ></Typography>
                  <Link to={history.length !== 0 && `/dashboad/videos/${history[0].videoId}`}><Button variant='outlined' sx={{ marginTop: "20px" }} startIcon={<PlayCircleIcon />}>Play all</Button>
                    <Button variant='outlined' sx={{ marginTop: "20px", marginLeft: "10px" }} startIcon={<ShuffleIcon />}>Shuffle</Button></Link>
                </Box>
              </Box>
            </Grid>
            <Grid item sx={{ width: { xs: "100%", md: "66.6%" } }}>
              <List sx={{ width: "100%", height: "90vh", overflowY: "scroll", marginTop: "59px" }}>
                {
                  history.map((elm, ind) => {
                    return (
                      <ListItem key={ind} >
                        <Link to={`/dashboad/videos/${elm.videoId}`} style={{ display: "flex", width: "100%" }} >
                          <Typography sx={{ height: "25px", margin: "auto 10px auto 5px", color: "#ccc" }}>{ind + 1}</Typography>
                          <Box className={'ListVideoContainer'} sx={{ width: { xs: "200px", sm: "200px", md: "200px" }, height: { xs: "10vh", sm: "15vh" } }}><video
                            preload='auto'
                            muted
                            src={`${baseURL}/public/videos/${elm.video.slice(14)}`} /></Box>
                          <Box width={"80%"}>
                            <Typography variant='h5' sx={{ width: { xs: "210px", sm: "300px", md: "80%" }, ml: 2, fontSize: { xs: "18px", sm: "20px", md: "25px", color: "#ccc" } }} >
                              {elm.title}
                            </Typography>
                            <Typography sx={{ ml: { xs: 2, md: 2 }, color: "#ccc" }}>Time's {new Date(elm.createdAt).toLocaleString()}</Typography>
                            <Divider />
                          </Box>
                        </Link>
                        <Box>
                          <MoreVertIcon sx={{ color: "#ccc" }} onClick={handleClick} />
                          <StyledMenu
                            id="demo-customized-menu"
                            MenuListProps={{
                              'aria-labelledby': 'demo-customized-button',
                            }}
                            anchorEl={anchorEl}
                            open={open}
                            onClose={handleClose}
                          >
                            <MenuItem disableRipple>
                              <WatchLaterRoundedIcon />
                              <Typography>Remove History</Typography>
                            </MenuItem>
                          </StyledMenu>
                        </Box>
                      </ListItem>
                    )
                  })
                }
              </List>
            </Grid>
          </Grid>
          }
        </Grid>
      </Grid>
    </>
  )
}

export default VideoHistory