import { Alert, Avatar, Box, Button, Input, Snackbar, Typography } from '@mui/material';
import background1 from '../assets/background/background1.mp4'

import React, { useState } from 'react';
import Axios from '../Axios';
import { Link } from 'react-router-dom';
import Cookies from 'universal-cookie'

const SignUp = () => {
    const cookie = new Cookies()
    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [confirmPassword, setConfirmPassword] = useState('')
    const [avatar, setAvatar] = useState('')
    const [open, setOpen] = useState(false)
    const [message, setMessage] = useState({})

    const submitHandler = async (e) => {
        e.preventDefault()
        try {
            const post = new FormData()
            post.append("name", name)
            post.append("email", email)
            post.append("password", password)
            post.append("confirmPassword", confirmPassword)
            post.append("image", avatar)
            const { data } = await Axios.post("/sign-up", post)
            cookie.set("token", data.token, { path: "/dashboad" })
            setMessage(data)
            setOpen(true)
            window.location.replace("/dashboad/home")
        } catch (error) {
            setMessage(error.response.data)
            setOpen(true)
        }
    }
    return (
        <>
        <Box sx={{position:'fixed',width:"100%",height:"100vh",zIndex:"-1"}}>
      <video style={{position:"absolute",width:"100%",height:"100%",objectFit:"cover"}}
      loop
      autoPlay
      muted
      src={background1}/>
    </Box>
            <Box component={"form"} onSubmit={submitHandler} className='container'
                width={"100%"} height={"100vh"} display={"flex"} justifyContent={"center"} alignItems={"center"}>
                <Box display={"flex"} width={"300px"}
                    height={"72vh"} flexDirection={"column"} justifyContent={"center"} alignItems={"center"}
                    className={"signUpContainer"} rowGap={2.5}>
                    <Avatar sx={{ marginTop: { xs: "-70px" }, width: "4rem", height: "4rem" }} />
                    <Input type='text'
                        autoComplete='of'
                        sx={{ width: "78%",color:"#ccc", marginTop: "30px" }}
                        value={name}
                        onChange={(e) => setName(e.target.value)}
                        placeholder='Enter Your Name' />
                    <Input type='email'
                        autoComplete='of'
                        sx={{ width: "78%",color:"#ccc" }}
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                        placeholder='Enter Your Email' />
                    <Input type='password'
                        autoComplete='of'
                        sx={{ width: "78%",color:"#ccc" }}
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                        placeholder='Enter Your Password' />
                    <Input type='password'
                        autoComplete='of'
                        sx={{ width: "78%",color:"#ccc" }}
                        value={confirmPassword}
                        onChange={(e) => setConfirmPassword(e.target.value)}
                        placeholder='Enter Your ConfirmPassword' />
                    <label htmlFor="inputTag" className="label">
                        <input type='file'
                            className='signUpProfile'
                            name='avatar'
                            accept="image/*"
                            onChange={(e) => setAvatar(e.target.files[0])}
                        />
                    </label>
                    <Button sx={{ width: "80%" }} variant='contained' type={"submit"}>sign up</Button>
                    <Typography sx={{ color: "rgb(190, 188, 188)", fontSize: "0.8rem" }}>You have already Account ?
                        <Link to={'/login'} style={{ paddingLeft: "10px", color: "#1976d2", fontSize: "0.9rem" }}>Login...</Link>
                    </Typography>
                </Box>
            </Box>
            <Snackbar open={open} autoHideDuration={3000} onClose={() => setOpen(false)}>
                <Alert onClose={() => setOpen(false)} variant='filled' severity={message.success ? "success" : "error"} sx={{ width: '100%' }}>
                    {message.message}
                </Alert>
            </Snackbar>
        </>
    )
}

export default SignUp