import React, { useContext, useEffect, useState } from 'react'
import Appbar from '../components/Appbar'
import Box from '@mui/material/Box';
import {CircularProgress, Grid, Typography } from '@mui/material';
import Video from '../components/Videos';
import ListContainer from '../components/List';
import Axios from '../Axios';
import { SearchContext } from '../context/search';
import logo from '../assets/background/logo.jpg'



const Home = ()=>{
  const [videos , setVideos] = useState([])
  const [loading , setLoading] = useState(false)
  const {search} = useContext(SearchContext)


  const fetchVideo = async()=>{
    try {
      setLoading(true)
      const {data:{show}} = await Axios.get("/showvideos")
      setLoading(false)
      setVideos(show)
    } catch (error) {
      setLoading(false)
      console.log(error)
    }
  }
  useEffect(()=>{
    fetchVideo()
  },[])

  const filter = videos.filter((elm)=>{
    return elm.title.toLowerCase().includes(search.toString().toLowerCase())
  })
    return(
        <>
        <Appbar/>
    <Grid container>
      <Grid item xs={2} sx={{display:{xs:"none",md:"block"}}}>
        <Box sx={{width:"16.5vw",borderRight:"1px solid lightGray",height:"90vh",position:"fixed"}}>
        <ListContainer/>
        </Box>
      </Grid>
      <Grid item xs={12} md={10}>
        <Grid container sx={{display:"flex",justifyContent:"center",gap:{xs:4,sm:2},marginTop:"75px"}} >
        {
          !loading?filter.map((video,ind)=>{
            return(<Grid item key={ind}><Video video={video}/></Grid>)
           }): <Box sx={{width:"100%",height:"85vh",display:"flex",justifyContent:"center",alignItems:"center"}}>
          <Box sx={{position:'relative'}} >
          <img
          style={{width:"50px",height:"50px",objectFit:"cover",position:"absolute",left:"5px",top:"0px"}}
          src={logo}
          alt='logo'
            aria-label="save"
            color="primary"
          />
          {loading && <CircularProgress size={60} sx={{marginRight:"-10px",marginTop:"-5px",color:"#ccc"}} />}
          <Typography sx={{color:"#ccc",fontSize:"20px",marginLeft:"-12px"}}>Loading...</Typography>
        </Box>
          </Box>
        }
       
        </Grid>
      </Grid>
    </Grid>
    
        </>
    )
}

export default Home